<template>
    <div class="joke">
      <transition name="fade">
        <explosion v-show="show"></explosion> 
      </transition>
      <div
          id="joke">
        <div 
          
          v-html="joke">
        </div>
        
        <img 
          src="data:image/png;base64,"
          @click="loadJoke"
          id="next" />
      </div>  
    </div>
</template>

<script>
import Explosion from './components/Explosion.vue'
export default {
  data() {
      return {
        joke: "",
        show: false
      };
  },
  methods: {
    loadJoke: function() {
      this.show = true
      this.joke = ""
      this.$http.get("https://api.icndb.com/jokes/random").then(result => {
        setTimeout(() => {
            this.show = false
            this.joke = result.body.value.joke
          }, 300
        )
      }, error => {
        console.log(error)
      })

      
      
    }
  },
  mounted() {
    this.loadJoke()
  },
  components: {
    'explosion': Explosion
  }
}
</script>

